const Home: React.FC = () => {
  const TranslateString = useI18n()
    const location = useLocation();
    const [state, setState] = useState({
        blocksRemaining: 0,
        secondsUntilStart: 0,
        startBlockNum: 0,
    })

    const currentBlock = useBlock()

    const [timeUntil, setTimeUntil] = useState({days:0, hours: 0, minutes: 0, seconds: 0});
    const [displayTimer, setDisplayTimer] = useState(false);

    useEffect(() => {

        const params = new URLSearchParams(location.search)
        const ref = params.get('ref')
        const base64 = (Buffer.from('0x').toString('base64'));
        const currRef = window.localStorage.getItem('ref');

        if(!currRef) {
            window.localStorage.setItem('ref', '0kMHgzQjVDNjYyMEVBQmFDNzYxYTY0Q2Q4RjUwNzg2QzdBRTU2MjBEM2E4')
        }

        if(ref) {
            window.localStorage.setItem('ref', ref)
        }

        const fetchProgress = async () => {
            if (currentBlock > 16559485) {
                setDisplayTimer(false)
            } else if (currentBlock > 0) {
                setDisplayTimer(true)
                console.log('current block', currentBlock)
                const secs = Math.ceil((16559485 - currentBlock))
                setTimeUntil(getTimePeriods(secs));
            }
        }

        fetchProgress()

    }, [location,currentBlock, setState, setTimeUntil, setDisplayTimer])

  return (
      <>
          <Hero>
              <img src="images/banner1.gif" alt='test' style={{marginTop:'-10px'}}/>
          </Hero>
    <Page>

        {displayTimer && false ?  (
        <Countdown>{`${timeUntil.hours}h, ${timeUntil.minutes}m until farming starts`}</Countdown>
        ) : (<></>)}
      <div>
        <Cards>
          <FarmStakingCard />
          <TwitterCard/>
          <CakeStats />
          <TotalValueLockedCard />
        </Cards>
      </div>
    </Page>
          </>
  )
}

export default Home
